import { use } from "i18next";
import LanguageDetector from "i18next-browser-languagedetector";
import { initReactI18next } from "react-i18next";

export function initializeI18N() {
  use(LanguageDetector)
    .use(initReactI18next)
    .init({
      resources: {
        en: {
          translation: {
            Copyright: " 2022 JK Ceramics Pvt Ltd. All rights reserved.",
          },
        },
        de: {
          translation: {
            Copyright: " 2022 JK Ceramics Pvt Ltd. Alle Rechte vorbehalten.",
          },
        },
        zh: {
          translation: {
            Copyright: " 2022 JK Ceramics Pvt Ltd. ",
          },
        },
        ar: {
          translation: {
            Copyright: " 2022 JK Ceramics Pvt Ltd.   .",
          },
        },
        ru: {
          translation: {
            Copyright: " 2022 JK Ceramics Pvt Ltd.   .",
          },
        },
      },
      fallbackLng: "en",
    });
}
